.App {
  text-align: center;
  background-color: #ffd662ff;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main_div {
  background-color: #00539cff;
  width: 300px;
  height: 400px;
  padding: 9px;
  border-radius: 14px;
} 

.s:hover{
  color: red;
  cursor: pointer;
  font-size: 25px;
}

.s{
  font-size: 19px;
  transition: all 0.4s ease;
}


/* .inpu{
  position: fixed;
} */
.df .j{
  transition: all 1.5s ease;
}

.df .j:hover{
  background-color: white;
  animation: an 1.5s  0.1s infinite;
}

.main_div input{
  height: 20px;
  width: 230px;
  margin-right: 9px;
  border-radius: 4px;
  outline: none;
  border:none;
}



.main_div .button{
  width: 37px;
  height: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: rgb(221, 72, 171);
  border-radius: 5px;
  text-align: center;
}


.j{
  margin: 8px;
  background-color: rgb(231, 219, 227);
  border-radius: 13px;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  place-items: center;
}


